/**
 *  实现 AlipayJSAPI 的浏览器端运行
 *  部分组件android ios风格存在差异 以ios风格实现
 *
 *  先实现项目已经用到的api ，实现功能列表如下
 *  toast
 *  showLoading hideLoading
 *  alert confirm
 *  exitApp
 *
 *  setTitle 未实现  subtitle，image 参数的api
 *  仅支持 设置标题， 副标题等其他不支持
 *
 *  返回
 *  第一次返回是关闭，这个还有待处理;
 *
 * setOptionMenu
 * 仅仅实现单个按钮的情景， 未实现 override ， menus ， icontype ， contentDesc 字段的api, preventDefault字段文档有错误
 *
 */
import './_main.scss';
import Loading from './loading';
import Toast from './toast';
import MessageBox from './message-box';
import Nav from './nav';
// const keys = ['toast', 'alert', 'confirm', 'showLoading', 'hideLoading'];

export default (arr) => {
  if (!_.isString(arr[0])) {
    throw new Error('arguments [0] must be String!');
  }
  
  switch (arr[0]) {
  case 'init':
    Nav.init();
    break;
  case 'toast':
    Toast.exec(arr);
    break;
  case 'showLoading':
    Loading.open(arr[1]);
    break;
  case 'hideLoading':
    Loading.close();
    break;
  case 'alert':
  case 'confirm':
    MessageBox(arr);
    break;
  case 'setTitle':
    Nav.setTitle(arr[1]);
    break;
  case 'showOptionMenu':
    Nav.showOptionMenu();
    break;
  case 'hideOptionMenu':
    Nav.hideOptionMenu();
    break;
  case 'setOptionMenu':
    arr.shift();
    Nav.setOptionMenu(arr[0]);
    break;
  case 'back':
    arr.shift();
    Nav.back(arr);
    break;
  case 'optionMenu':
    arr.shift();
    Nav.optionMenu(arr);
    break;
  case 'exitApp':
    Nav.close();
    break;
  default:
    console.error('brower not support this api');
  }
};



