<script>
import { Toast } from 'mint-ui';
  
export default {
  exec (arr) {
    arr.shift();
    let convent = {};
    convent.className = 'zfb-toast';
    convent.message = arr[0].content || '';
    arr[0].duration && (convent.duration = arr[0].duration);
    if (['success', 'fail', 'exception'].indexOf(arr[0].type) !== -1) {
      convent.iconClass = `zfb-icon zfb-icon_${arr[0].type}`;
    }
    Toast(convent);
    if (arr[1] && _.isFunction(arr[1])) {
      setTimeout(() => {
        arr[1]();
      }, convent.duration || 0);
    }
  }
};
</script>



