<style type='text/scss' lang="scss">
  @import "../../../../scss/variables";
  @import "../../../../scss/minix";

  $name: 'zfb-msgbox';

  .#{$name} {

  }

  .#{$name}-wrapper {
    @include fixed-center();
    color: #000;
    background-color: $white;
    width: 85%;
    border-radius: .06rem;
    font-size: .16rem;
    -webkit-user-select: none;
    overflow: hidden;
    backface-visibility: hidden;
    padding-top: .17rem
  }

  .#{$name}-header {
    font-size: .17rem;
    line-height: 1.2;
    min-height: .26rem;
    padding: .05rem .2rem .05rem .2rem;
  }

  .#{$name}-content {
    font-size: $font-size-base;
    color: $gray;
    padding: .05rem .3rem .05rem .3rem;
    min-height: .25rem;
  }

  .#{$name}-btns {
    margin-top: .06rem;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: .5px;
    // @include border(top); //在（政务网app+安卓机）上不显示，故添加::after来代替@include border(top)
    position:relative;
    &::after{
      position: absolute;
      content:'';
      top:0;
      left:0;
      width:100%;
      height:1px;
      background-color: #ddd;
      transform: scaleY(0.5);
    }
    .btn {
      flex: 1;
      border: 0;
      background-color: $white;
      padding: 0;
      border-radius: 0;
      font-size: $font-size-large;
      line-height: 2.4;
      color: #000;

      &:nth-of-type(2) {
        @include border(left);
        color: $main-blue;
      }
      &:only-child {
        color: $main-blue;
      }
      &:active {
        background-color: $gray-lighter;
      }
    }
  }

  .#{$name}-mask {
    @include mask();
    opacity: 0.65;
    background: #000;
  }
</style>

<template>
  <div class="zfb-msgbox" v-show="show">
    <div class="zfb-msgbox-wrapper">
      <div class="zfb-msgbox-header" v-show="config.title">
        {{config.title}}
      </div>
      <div class="zfb-msgbox-content" v-show="config.message" :style="{textAlign: config.align}">
        {{config.message}}
      </div>
      <div class="zfb-msgbox-btns">
        <button class="btn" v-show="type !== 'alert'" @click="cancel">{{config.cancelButton}}</button>
        <button class="btn" @click="sure">{{type !== 'alert' ? config.okButton : config.button}}</button>
      </div>
    </div>
    <div class="zfb-msgbox-mask" @touchmove.stop.prevent>
    </div>
  </div>
</template>

<script type="text/babel">
export default {
  name: 'zfb-message-box',
  data () {
    return {
      show: false,
      type: 'alert',
      config: {
        title: '',
        message: '',
        align: 'center',
        button: '确定',
        okButton: '确定',
        cancelButton: '取消'
      }
    };
  },
  methods: {
    exec (arr) {
      this.type = arr.shift();
      Object.assign(this.config, {align: 'center'}, arr.shift());
      this.callback = arr.shift() || function () {};
      this.show = true;
    },
    cancel () {
      this.show = false;
      this.callback({'ok': false});
    },
    sure () {
      this.show = false;
      let cbParams = {};
      if (this.type !== 'alert') {
        cbParams.ok = true;
      }
      this.callback(cbParams);
    }
  }
};
</script>



