<template>
  <div class="zfb-nav">
    <div class="zfb-nav-wrapper">
      <button class="button button-clear left" @click="backEvent" :style="{'visibility': showBack ? 'visible' : 'hidden'}">
        <i class="zfb-icon zfb-icon_back"></i>
        <span class="text">返回</span>
      </button>
      <div class="title">{{title}}</div>
      <button class="button button-clear right" @click="menuEvent" :style="{'visibility': showOptionMenu ? 'visible' : 'hidden'}" ref="optionMenu">
        <span v-show="option.title || option.icon" ref="menu" class="menu-text" :class="{'has-icon': option.icon}">{{option.title}} <i class="icon" v-if="option.redDot">{{option.redDot}}</i></span>
        <i class="zfb-icon zfb-icon_menu" v-show="!option.title && !option.icon"></i>
      </button>
    </div>
    <form @submit="helpSubmit" class="help-event">
      <input id="back" type="submit" ref="helpToSubmit" value=" "/>
    </form>
  </div>
</template>

<style type='text/scss' lang="scss">
  @import "../../../../scss/variables";
  @import "../../../../scss/minix";

  $name: 'zfb-nav';

  .#{$name} {
    height: .44rem;
    .help-event {
      display: none;
    }
  }

  .#{$name}-wrapper {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    height: .44rem;
    z-index: 9999;
    background-color: $white;
    display: flex;
    align-items: center;
    text-align: center;
    @include border(bottom);
    text-overflow: ellipsis;
    white-space: nowrap;

    .button {
      width: .9rem;
      color: $main-blue;
      font-size: .19rem;
      user-select: none;
      &:active {
        opacity: .7;
      }
    }
    .button-clear {
      border-color: transparent;
      background: none;
      box-shadow: none;
      border-width: 0;
      padding: 0;
    }
    .zfb-icon {
      vertical-align: top;
      pointer-events: none;
      font-size: $font-size-larger;
    }
    .left {
      .text {
        margin-left: -.06rem;
      }
    }
    .right {
      text-align: right;
      padding-right: .16rem;
      .has-icon {
        width: .3rem;
        height: .24rem;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: 50%;
      }
      .menu-text {
        display: inline-block;
        position: relative;
        .icon {
          position: absolute;
          top: -.05rem;
          color: $white;
          right: -.1rem;
          background-color: #f00;
          border-radius: 50%;
          font-size: .1rem;
          font-style: normal;
          height: .15rem;
          width: .15rem;
          line-height: .15rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          text-align: center;
        }
      }
    }
    .title {
      flex: 1;
      font-size: .21rem;
      font-weight: 600;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
</style>

<script>
export default {
  data () {
    return {
      title: document.title,
      showBack: true,
      showOptionMenu: true,
      isSetedOptionMenu: false,
      option: {
        title: '',
        icon: '',
        redDot: '',
        color: ''
      }
    };
  },
  methods: {
    backEvent () {
      this.$refs.helpToSubmit.click();
    },
    back (config, fun) {
      this.$refs.helpToSubmit[config.type + 'EventListener']('click', fun, config.useCapture || false);
    },
    helpSubmit (e) {
      e.preventDefault();
      _bus._router.back();
    },

    close () {
      let userAgent = navigator.userAgent;
      if (userAgent.indexOf('Firefox') != -1 || userAgent.indexOf('Chrome') != -1) {
        window.location.href = 'about:blank';
      } else if (userAgent.indexOf('Android') > -1 || userAgent.indexOf('Linux') > -1) {
        window.opener = null;
        window.open('about:blank', '_self', '').close();
      } else {
        window.opener = null;
        window.open('about:blank', '_self');
        window.close();
      }
    },

    menuEvent () {
      if (!this.isSetedOptionMenu) {
        alert('这是默认的分享事件');
      }
      this.option.redDot = '';
    },
    optionMenu (config, fun) {
      this.$refs.optionMenu[config.type + 'EventListener']('click', fun, config.useCapture || false);
    },
    setOptionMenu (config) {
      this.isSetedOptionMenu = true;
      if (config.reset) {
        this.option.title = '';
        this.option.icon = '';
        this.isSetedOptionMenu = false;
      } else if (config.title) {
        this.option.title = config.title;
        this.option.icon = '';
      } else {
        this.option.title = '';
        this.option.icon = config.icon;
      }
      this.option.redDot = config.redDot;

      if (config.color) {
        let colors = [config.color.substring(3, 5), config.color.substring(5, 7), config.color.substring(7, 9), config.color.substring(1, 3)];
        colors = colors.map(item => parseInt(item, 16));
        if (colors[colors.length - 1] == '00') {
          colors[colors.length - 1] = '1';
        } else {
          colors[colors.length - 1] = colors[colors.length - 1] / 256;
        }
        this.option.color = `rgba(${colors.join(',')})`;
      } else {
        this.option.color = '';
      }
    }
  },
  watch: {
    'option.icon' (n, o) {
      this.$nextTick(() => {
        if (n) {
          this.$refs.menu.style.backgroundImage = `url(${n})`;
        } else {
          this.$refs.menu.style.backgroundImage = '';
        }
      });
    },
    'option.color' (n, o) {
      this.$nextTick(() => {
        if (n) {
          this.$refs.menu.style.color = n;
        } else {
          this.$refs.menu.style.color = '';
        }
      });
    }
  }
};
</script>



