<template>
  <transition name="zfb-loading">
    <div class="zfb-loading" v-show="visible">
      <div class="zfb-loading-wrapper" :style="{ 'padding': text ? '.2rem' : '.15rem' }">
        <span class="snake"></span>
        <span class="text" v-show="text">{{ text }}</span>
      </div>
      <div class="zfb-loading-mask" @touchmove.stop.prevent></div>
    </div>
  </transition>
</template>

<style type='text/scss' lang="scss">
  @import "../../../../scss/variables";
  @import "../../../../scss/minix";

  $name: 'zfb-loading';

  .#{$name} {
    transition: opacity .2s linear;
  }

  .#{$name}-wrapper {
    @include fixed-center();
    color: $white;
    border-radius: .05rem;
    background-color: #333;
    .text {
      display: block;
      color: #fff;
      text-align: center;
      font-size: .16rem;
    }
    .snake {
      display: inline-block;
      animation: #{$name}-rotate 1s infinite linear;
      border: .04rem solid transparent;
      border-radius: 50%;
      border-top-color: rgb(204, 204, 204);
      border-left-color: rgb(204, 204, 204);
      border-bottom-color: rgb(204, 204, 204);
      border-right-color: $main-blue;
      height: .32rem;
      width: .32rem;
    }
  }

  @keyframes #{$name}-rotate {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }

  .#{$name}-mask {
    @include mask();
    opacity: 0;
    background: transparent;
  }

  .#{$name}-enter,
  .#{$name}-leave-active {
    opacity: 0;
  }
</style>

<script>
export default {
  data () {
    return {
      visible: false
    };
  },

  props: {
    text: String
  }
};
</script>



