import Vue from 'vue';
import { remSize } from '../../utils';
import mainNav from './main.vue';
const Nav = Vue.extend(mainNav);

let instance;

export default {
  init () {
    instance = new Nav({
      el: document.createElement('div')
    });
    document.body.insertBefore(instance.$el, document.getElementById('app'));
    document.getElementById('app').setAttribute('style', `height: ${document.body.clientHeight - 0.44 * remSize}px;position:relative;`);
  },
  setTitle (config) {
    this.helpUtil('title', config.title);
    document.title = config.title;
  },
  close () {
    instance.close();
  },
  showOptionMenu () {
    this.helpUtil('showOptionMenu', true);
  },
  hideOptionMenu () {
    this.helpUtil('showOptionMenu', false);
  },
  helpUtil (key, val) {
    if (instance) {
      instance[key] = val;
    } else {
      setTimeout(() => {
        instance[key] = val;
      }, 50);
    }
  },
  setOptionMenu (config) {
    instance.setOptionMenu(config);
  },
  back (arr) {
    instance.back(...arr);
  },
  optionMenu (arr) {
    instance.optionMenu(...arr);
  }
};



